@tailwind base;
@tailwind components;
@tailwind utilities;


@keyframes fade-in-out {
    0% {
      opacity: 0;
    }
    25% {
      opacity: 1;
    }
    75% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
}
  
.animate-fade-in-out {
    animation: fade-in-out 1s ease-in-out;
}